itions de l'entrée sur le territoire Camerounais. Le simple fait qu'un visa m'ait été accordé n'implique pas que j'aurai droit attachés aux citoyens du territoire, je dois prendre des informations auprès des services compétentes pour connaitre les droits qui me sont accordés. ")]),_c('div',{staticClass:"cell1"}),_c('div',{staticClass:"cell1"},[_vm._v("Signature (pour les mineurs, signature de l’autorité parentale/du tuteur légal) : ")])])])])}]

